@import '../../../index.scss';

.welcome {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 512px;
    background-color: #FFFF;
    @extend .container;
    >div {
        margin: 0px 40px;
    }
}
.storeImg {
    img {
        width: 550px;
        height: 350px;
    }
}
.welcome__text {
    display: flex;
    flex-direction: column;
    align-items: start;
    .welcome__text_title {
        font-weight: 600;
        color: #222222;
        font-size: 24px;
    }
    .welcome__text_subTitle {
        color: #7B7B7B;
        font-size: 15px;
    }
    .welcome__text_maintext {
        font-size: 14px;
        color: #8D8D8D;
    }
    .btn_contactUs {
        margin: 30px 0px;
        a {
            text-decoration: none;
            border: 1px solid #E5E5E5;
            background: #EEEEEE;
            text-transform: uppercase;
            font-family: $Raleway;
            font-size: 12px;
            color: #222222;
            padding: 16px 28px;
            &:hover {
                opacity: 0.9;
            }
        }
    }
}

@media screen and (max-device-width: 1024px) {
    .welcome {
        width: 840px;
        >div {
            margin: 0px 20px;
        }
    }
    .storeImg {
        img {
            width: 400px;
            height: 300px;
        }
    }
    .welcome__text {
        .welcome__text_title {
            font-weight: 600;
            color: #222222;
            font-size: 16px;
        }
        .welcome__text_subTitle {
            color: #7B7B7B;
            font-size: 12px;
        }
        .welcome__text_maintext {
            font-size: 12px;
            color: #8D8D8D;
        }
        .btn_contactUs {
            a {
                font-size: 10px;
            }
        }
    }
}