@import '../../../index.scss';

.servicesWrapper {
    border: 1px solid #F0F0F0;
    background-color: #F9F9F9;
    height: 362px;
    display: flex;
    align-items: center;
}
.services {
    display: flex;
    justify-content: space-between;
    @extend .container;
    .service {
        text-align: center;
        width: 320px;
        .service__title {
            color: #222222;
            font-size: 15px;
        }
        .service__text {
            font-family: $Roboto;
            color: #888888;
            font-size: 14px;
        }   
    }
}

@media screen and (max-device-width: 1024px) {
    .services {
        width: 840px;
        .service {
            width: 220px;  
        }
    }
}